
import XButton from 'vux/src/components/x-button/index.vue'
import Alert from 'vux/src/components/alert/index.vue'

import api from "../api.js";
import axios from "axios";
import util from "../util";
export default {
components: {
XButton,
Alert
},
props: {
id: {
type: Number,

default: 0
},
name: {
type: String,

default: 0
}
},

data() {
return {
share_title:'分享一下',
alert_show:false,
value: "今天",
actname: "",
actid: 0,
act: {
id: 1,
type: "讲座",
name: "这是页面初始模拟数据",
valid_date: "2019-09-09",
apply_way: "这是参与方式的模拟数据",
school: "华南理工大学",
taglist: "有趣,无聊,妹子多",
url: "http://www.scut.edu.cn",
litimg_url: "http://www.scut.edu.cn",
pic_url: "http://www.scut.edu.cn",
location: "华南理工大学",
act_detail: "ActsBoard是一家五百强企业，融资400个亿..."
},
have_pic:true
};
},
methods: {
onChange() {

},
get_activity_by_id(id) {
var _this = this;
axios
.get(api.get_activity_by_id(id))
.then(function(response) {
console.log(response.data);

response.data.data.valid_date = response.data.data.valid_date.split(' ')[0];



_this.act = response.data.data;
if(response.data.data.pic_url.length == 0){
console.log('改了')
_this.have_pic = false;
}
util.wx_act_detail_share(response.data.data.name,window.location.href)

})
.catch(function(error) {
console.log(error);
});
},
to_share() {
this.alert_show = true;
setTimeout(() => {
this.alert_show = false;
}, 3000)
}
},
mounted: function() {



console.log(
"这是已经跳转到活动详情组件的id号码" + this.$route.params.actid
);
this.actid = this.$route.params.actid;
this.actname = this.$route.params.actname;
util.to_wx_config();
console.log(this.actid);
var that = this;
this.get_activity_by_id(this.actid);

},
beforeCreate(){

}

};



