
import Selector from 'vux/src/components/selector/index.vue'
import Group from 'vux/src/components/group/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Picker from 'vux/src/components/picker/index.vue'

import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'


export default {
components: {
PopupPicker,
Picker,
Group,
Selector,
Flexbox,
FlexboxItem
},
data() {
return {
value_time: ["所有"],
value_type: ["全部类别"],

timeList: [["所有", "未来三天", "未来七天"]],
typeList: [
[
"全部类别",
"社团招新",
"企业宣讲",
"公益",
"讲座",
"比赛",
"运动",
"其他"
]
]
};
},
methods: {
onChange() {

console.log("select 组件提交")

if (this.value_time[0] == "所有")
this.$emit("select", 0, this.value_type[0]);
if (this.value_time[0] == "未来三天")
this.$emit("select", 3, this.value_type[0]);
if (this.value_time[0] == "未来七天")
this.$emit("select", 7, this.value_type[0]);
}
}
};



