
import activityOutline from "./activityOutline";
import axios from "axios";
import api from "../api";
import util from "../util";
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'



export default {
components: {
activityOutline,

Flexbox,
FlexboxItem
},
name: "tab",
data() {
return {
activities: [
{










}
]
};
},
methods: {
get_activities_t() {


var _this = this;
axios
.get(api.get_recommend_activity) //这个api没换成推荐的API
.then(function(response) {
if(response.data.errmsg == 2001){
alert('您没有设置推送规则,请先设置推送规则')
_this.$router.push({ path: "/setting" });
}
console.log(response.data);
for(let i = 0;i < response.data.data.length;i++){
response.data.data[i].valid_date = response.data.data[i].valid_date.split(" ")[0];
}
_this.activities = response.data.data;
util.wx_common_share();

})
.catch(function(error) {
console.log(error);
});
},
to_activities() {
this.$router.push({ path: "/" });
},
to_setting() {
this.$router.push({ path: "/setting" });
},
to_act_detail(id,name) {
console.log("即将跳转的活动id是" + id);

this.$router.push({
name: "activityDetail",
params: {
actid: id,
actname:name
}
});
}
},
mounted: function() {
util.to_wx_config();
var that = this;
util.getCode("actpushing",that.get_activities_t);

},


created(){





}
};



